<template>
    <div>
        <br>
        <div class="row">
            <div class="offset-1 col-9 bg-white rounded-lg shadow-lg p-3">
         <div class="p-5">
          <p class="text-2xl">Ekstrakulikuler</p>
            <hr class="style6">
            <br>
            <img :src="`https://infolayanans.space/${this.berita.gambar}`" class="kinoLightBox img-fluid  " style="border-radius: 20px;width:100%;">
            <p class="font-bold p-3 text-uppercase font-times text-2xl">{{berita.judul}}</p>
            <p class="float-right text-sm font-italic">{{format(berita.tanggal)}}</p>
            <p class="font-times font-italic">Posted by : <span class="text-green">{{berita.author}}</span></p>
            <p class="text-justify text-xl" v-html="berita.isi_berita"></p>
       </div>
            </div>
        </div>
<br>
<br>
<br>
    </div>
</template>
<script>
var id = require("date-fns/locale/id");
import axios from 'axios'
export default{
    data(){
        return{
            berita:{}
        }
    },
    methods: {
          format(date) {
        return this.$datefns.format(
            new Date(date),
            "dddd DD MMMM YYYY",
            {
            locale: id
            }
        );
        },
    },
    layout:"awal",
    mounted() {
        let fd = new FormData()
        fd.append('data', `select * from sekolah_berita where id_berita='${this.$route.params.idberita}'`)//database setting di server
        fd.append('database','infolay3_test')
        axios.post('https://infolayanans.space/api/mysql/auto.php', fd
        , {
            headers: {
            Authorization: localStorage.getItem('auth._token.local')
            }
        }).then(res => {
            console.log(res)
            this.berita=res.data[0]
            this.$forceUpdate()
        })
    },
}
</script>